.mainContainer {
  display: flex;
  height: 515px;
}
.leftBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-family: 'Heebo', sans-serif;
}
.helloBlock {
  width: 506px;
  height: 105px;
  flex-shrink: 0;
  margin-left: 140px;
}
.textBlock {
  width: 497px;
  height: 66px;
  margin-top: 40px;
  margin-left: 140px;
}
.rightBlock {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.photoContainer {
  width: 243px;
  height: 243px;
  border-radius: 50%; /* Делает элемент круглым */
  background: url('../../../public/photo/mih.jpg') lightgray 50% / cover no-repeat;
}
